.createMemePage3 {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.createMemePage3Content {
  height: calc(100vh - 100px);
  width: 100%;
  position: relative;
  box-sizing: border-box;
}

.createMemeBlock3 {
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 120px;
  width: 100%;
  grid-template-columns: 1fr 500px 1fr;
  align-items: center;
}

@media (max-width: 1100px) {
  .createMemeBlock3 {
    grid-template-columns: 400px;
    grid-template-rows: 400px 1fr;
    justify-content: center;
  }
  .picMeme3 {
    grid-column-start: 1;
    grid-column-end: 2;
    width: 400px;
    height: 400px;
  }
}

.picMeme3 {
  grid-column-start: 2;
  grid-column-end: 3;
  width: 500px;
  height: 500px;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 10px;
}

.btnSaveWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}